.form-send-tweet {
    background-color: white;
    border: 2px solid #00acee;
    padding: 20px;
    width: 600px;

    &__title {
        margin: 0 !important;
    }

    &__form {
        .MuiFormControl-root {
            width: 100%;
        }

        &-name {
            width: 50% !important;

        }

        &-textarea {
            width: 100%;
        }
    }
}